var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"page-international-students _page_form_campus typography"},[_vm._ssrNode("<body class=\"page-template page-template-page-template page-template-international_student_support page-template-page-templateinternational_student_support-php page page-id-2029\">","</body>",[_vm._ssrNode("<div id=\"page\" class=\"site\">","</div>",[_c('HeaderNav'),_vm._ssrNode(" <div id=\"content\" class=\"site-content main-header\"><div class=\"banner-def\"><img src=\"/images/banner/banner_1.png\" alt> <div class=\"banner_mobi\"><img src=\"/images/banner/banner_1.png\" alt></div> <div class=\"container_common\"><div class=\"content_common\"><div class=\"content\"><div class=\"top\"><a"+(_vm._ssrAttr("href",_vm.localePath('/')))+">Home</a> <span>/</span> <a href=\"#\">Hệ thống nhượng quyền</a></div>\n                Hệ thống nhượng quyền\n              </div></div></div></div> <div class=\"content-page\"><div class=\"_decor_top\"><img src=\"/images/page/page-decor-top.png\" alt></div> <div class=\"decor_bottom\"><img src=\"/images/page/page-decor-bottom-left3.png\" alt></div> <div class=\"container_common\"><div class=\"content_common\"><div class=\"title-def\"><img src=\"/images/common/star.png\" alt=\"International Students Support\">Hệ thống nhượng quyền\n              </div> "+(_vm._ssrList((2),function(i){return ("<div class=\"box_highlight_of_vietnam\" style=\"margin-bottom: 0\">"+(_vm._ssrList((2),function(i){return ("<div class=\"item\"><img src=\"/uploads/2022/01/Hanoi.jpg\" alt> <div class=\"_text\"><h3>Hanoi</h3> <p><span lang=\"EN-US\" xml:lang=\"EN-US\" data-contrast=\"auto\" class=\"TextRun SCXW202581741 BCX0\"><span class=\"NormalTextRun SCXW202581741 BCX0\">Culture and history are the key aspects of this\n                          1000-year-old city. Walk the narrow historic streets\n                          of the Old Quarter which contains 36 streets, with\n                          shops selling everything from red candlesticks to\n                          lacquerware and water puppets, and then go for a\n                          stroll to take in the sights and sounds around Hoan\n                          Kiem Lake. The lake is the focal point of Hanoi\n                          life, especially on weekends. The Turtle Tower\n                          (‘</span><span class=\"NormalTextRun SpellingErrorV2 SCXW202581741 BCX0\">Tháp</span><span class=\"NormalTextRun SCXW202581741 BCX0\"></span><span class=\"NormalTextRun SpellingErrorV2 SCXW202581741 BCX0\">Rùa</span><span class=\"NormalTextRun SCXW202581741 BCX0\">’ in Vietnamese) was built in the 18th Century and </span><span class=\"NormalTextRun SCXW202581741 BCX0\">is located in</span><span class=\"NormalTextRun SCXW202581741 BCX0\">\n                          the </span><span class=\"NormalTextRun SpellingErrorV2 SCXW202581741 BCX0\">centre</span><span class=\"NormalTextRun SCXW202581741 BCX0\">\n                          of the lake, on a small island. You’ll also find\n                          plenty of street traders, cafés, restaurants,\n                          tourist shops and art galleries in this historical\n                          area.</span></span><span data-ccp-props=\"{\"201341983\":0,\"335559739\":160,\"335559740\":259}\" class=\"EOP SCXW202581741 BCX0\"></span></p></div></div>")}))+"</div>")}))+" <div class=\"title-def\" style=\"margin-top: 32px\"><img src=\"/images/common/star.png\" alt=\"International Students Support\">Tại sao lại chọn hệ thống nhượng quyền của BES\n              </div> <div class=\"content_preparing the_content_def\"><p>\n                  The Student Experience Department is designed for enriching\n                  and unlocking student potential at the University alongside\n                  providing a vast array of opportunities to help students\n                  develop a path to explore their future-orientated options.\n                  Alongside this, the department aims to highlight the\n                  essential skills required to prepare them for life after\n                  college, grow as well-rounded individuals, and connect\n                  students to future career possibilities.\n                </p> <p>\n                  The department can be found hosting a wide range of\n                  extracurricular activities offered throughout the year to\n                  support students in growing as individuals, embracing\n                  diversity, exploring talents, cultivating leadership skills,\n                  and enhancing their emotional intelligence. All these\n                  efforts are put in place with the aim of building global\n                  citizens who engage in their local communities and contexts.\n                </p> <p>\n                  So, let’s check out what you need to prepare before coming\n                  to BES for your study.\n                </p></div> <h3>Essential items on Vietnam arrival</h3> <div class=\"box_essential_items\"><div class=\"_left\"><img src=\"/uploads/2021/12/1710TM-Camtrip-113-scaled-e1639977036883.jpg\" alt></div> <div class=\"_right\" style=\"\\n                    background: url('/images/admission/bgr1.png');\\n                  \"><div class=\"box_content the_content_def\"><div class=\"_item\"><h3>Passport</h3> <p></p></div> <div class=\"_item\"><h3>Visa</h3> <p></p></div> <div class=\"_item\"><h3>Offer letter</h3> <p>Offer letter from British University Vietnam</p></div> <div class=\"_item\"><h3>Key contact</h3> <p>\n                        Key contact name/ phone numbers from BES Student Life\n                        Team\n                      </p></div> <div class=\"_item\"><h3>Currency</h3> <p>\n                        Vietnamese dong (or you can exchange money at the\n                        airport). It is also easier to bring money to get\n                        exchanged since you usually get a better exchange rate\n                        for bigger notes\n                      </p></div> <div class=\"_item\"><h3>Driving license</h3> <p>\n                        Driving license (or another identification document).\n                      </p></div> <div class=\"_item\"><h3>Personal valuables</h3> <p>\n                        All personal valuables: laptop, phone, camera, etc.\n                      </p></div> <div class=\"_item\"><h3>Travel documents</h3> <p>\n                        Have photocopies of all your key travel documents in\n                        your main luggage\n                      </p></div></div></div></div> <div class=\"box__content__text the_content_def\"><p><strong>ACCOMMODATION</strong></p> <p>\n                  BES can connect you to a recommended agent who can help you\n                  find accommodation in Ecopark. Renting serviced and\n                  furnished apartments is very common in Vietnam. Don’t worry\n                  – BES Student Life will assign you with a Vietnamese student\n                  who will become your ‘global buddy’ and help you out.\n                </p> <p><strong>TRANSPORTATION</strong></p> <p>\n                  The most popular way to get around is by motorbike. However,\n                  for foreign students, the easiest way to travel by bike is\n                  to book a driver via. an app which there are a number of\n                  popular brands in Vietnam, for instance, Gojek, Grab, Be,\n                  etc. and helmets are compulsory. Buses and taxies are also\n                  available, and fares in Vietnam are very affordable. Longer\n                  distance travel to other Vietnamese cities or into the\n                  countryside can be done by long-distance bus, train, and by\n                  air.\n                </p></div> <div class=\"the_content_def section_global_buddy\"><h3>Global Buddy Programme</h3> <p></p> <p>\n                  This programme aims to deepen students’ understanding of\n                  themselves and other identities by fostering an\n                  understanding of various dimensions of culture and an\n                  appreciation of cultural and human differences. Global Buddy\n                  team is a group of BES volunteers who dedicate their time\n                  and commitment to support international students with on and\n                  off-campus services. Buddies will be the first contact point\n                  prior to departure from your home country and will stay in\n                  contact with you for the whole exchange period. As local\n                  students, BES buddies have strong knowledge and\n                  understanding about Vietnamese culture and will be a great\n                  hand to help you make the most of your time in Vietnam.\n                  Buddies will support international students to learn how to\n                  cope with cultural and social diversity and the nuances of\n                  Vietnamese culture. Together, they will explore how-to\n                  live-in harmony and connect within the community.\n                </p> <p>\n                  The Global Buddy team organises activities for International\n                  Students together with BES Student Life Team throughout your\n                  semester in Vietnam. The activities include:\n                </p> <div class=\"_left\"><img src=\"/uploads/2021/12/Hanoi-2.jpg\" alt=\"International Students Support\"></div> <div class=\"_right the_content_def\" style=\"\\n                    background-image: url('/images/admission/ban_do_viet_nam.png');\\n                  \"><ul><li>Airport pickup services</li> <li>International orientation</li> <li>City tours and Food tours</li> <li>Welcome activities and dinner</li> <li>Vietnamese language classes</li> <li>Vietnamese culture sharing</li> <li>Community engagement trip</li> <li>Exploring Vietnam</li></ul></div></div></div></div></div></div> "),_c('FooterSite')],2)])])
